import { useState, useEffect } from 'react'
import fileDownload from 'js-file-download'
import {
  Paper,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Button,
  Tabs,
  Tab
} from '@material-ui/core'
import { 
  AddCircle as AddCircleIcon,
  ArrowBack as ArrowBackIcon,
  Delete as DeleteIcon,
  ArrowForward as ArrowForwardIcon,
  SettingsBackupRestore as SettingsBackupRestoreIcon
} from '@material-ui/icons'
import styled from 'styled-components'
import ColorPicker from 'material-ui-color-picker'

import { IconHover } from './styles'
import RateBoard from '../RateBoard/RateBoard'
import ColumnCard from './ColumnCard'
import useConfigReducer from './useConfigReducer'

const Container = styled.div`
  h1 {
    text-align: center;
  }
`
const PaperContainer = styled(Paper)`
  margin: 0.5rem;
  padding: 1rem;
`
const SubTitle = styled.h2`
  text-align: center;
  margin: 0;
  margin-bottom: 1rem;
`
const PaperCard = styled(Paper)`
  padding: 0.5rem;
  width: 10rem;
`
const BaseAddContainer = styled(Paper)`
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  :hover {
    cursor: pointer;
    background-color: #f9f9f9;
  }
`
const AddColumnContainer = styled(BaseAddContainer)`
  width: calc(10rem + 16px);
  min-height: 12rem;
`
const AddCarouselImageContainer = styled(BaseAddContainer)`
  width: calc(10rem + 16px);
  min-height: 6rem;
`
const CenterButtonContainer = styled.div`
  padding: 1rem;
  display: flex;
  justify-content: center;
`
const TextFieldContainer = styled.div`
  width: 7rem;
`

const colourColumns = [
  { label: 'Background', key: 'backgroundColour' },
  { label: 'Header', key: 'headerColour' },
  { label: 'Even Row', key: 'evenColour' },
  { label: 'Odd Row', key: 'oddColour' },
  { label: 'Divider', key: 'dividerColour' },
  { label: 'Font', key: 'fontColour' },
  { label: 'Footer', key: 'footerColour' },
  { label: 'Weather Background', key: 'weatherBackgroundColour' },
  { label: 'Weather Font', key: 'weatherFontColour' },
]

const miscColumns = [
  { label: 'Logo', key: 'logo' },
  { label: 'Max Rows', key: 'maxRows', type: 'number' },
  { label: 'Rows Interval', key: 'rowsInterval', type: 'number' },
  { label: 'Date Position', key: 'datePosition', options: [
    { label: 'None', key: 'none' },
    { label: 'Top Left', key: 'top-left' },
    { label: 'Top Right', key: 'top-right' },
    { label: 'Bottom Left', key: 'bottom-left' },
    { label: 'Bottom Right', key: 'bottom-right' },
  ] }
]

const weatherColumns = [
  { label: 'API Key', key: 'weatherApiKey' },
  { label: 'Refresh Rate', key: 'weatherInterval', type: 'number' },
  { label: 'Position', key: 'weatherPosition', options: [
    { label: 'None', key: 'none' },
    { label: 'Left', key: 'left' },
    { label: 'Right', key: 'right' },
  ] },
  { label: 'Units', key: 'weatherUnits', options: [
    { label: 'Metric', key: 'metric' },
    { label: 'Imperial', key: 'imperial' },
    { label: 'Kelvin', key: 'standard' },
  ] }
]

const Config = ({ defaultConfig, catalog }) => {
  const [config, dispatcher] = useConfigReducer();
  const [showPreview, setShowPreview] = useState(false);
  const [tabValue, setTabValue] = useState('columns');

  useEffect(() => {
    dispatcher.setDefault(defaultConfig)
  }, [defaultConfig]);

  const onColumnAdd = () => {
    dispatcher.onPushArray('tableColumns', { title: '', key: '', fontSize: 'medium', align: 'left' })
  }

  const onAddCarouselImage = () => {
    dispatcher.onPushArray('carouselImages', { src: '' })
  }

  return (
    <Container>
      <h1>Rate Board Configuration</h1>

      <PaperContainer elevation={3} style={{ padding: 0 }}>
        <Tabs
          value={tabValue}
          onChange={(e, value) => setTabValue(value)}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          <Tab value="columns" label="Columns" />
          <Tab value="colours" label="Colours" />
          <Tab value="misc" label="Misc" />
          <Tab value="carousel" label="Carousel" />
          <Tab value="weather" label="Weather" />
        </Tabs>
      </PaperContainer>

      <PaperContainer elevation={3}>
        {tabValue === 'columns' && (
          <>
          <SubTitle>Columns</SubTitle>

          <Grid container direction="row" spacing={2}>
            {config.tableColumns?.map((col, index) => (
              <Grid item>
                <PaperCard elevation={1}>
                  <ColumnCard
                    isFirst={index === 0}
                    isLast={index === config.tableColumns.length - 1}
                    column={col}
                    onColumnShift={shift => dispatcher.onShiftArray('tableColumns', index, shift)}
                    onColumnRemove={() => dispatcher.onRemoveArrayObject('tableColumns', index)}
                    onColumnChange={(e, key) => dispatcher.onChangeArrayObject('tableColumns', e, index, key)}
                  />
                </PaperCard>
              </Grid>
            ))}

            <Grid item>
              <AddColumnContainer onClick={onColumnAdd}>
                <AddCircleIcon fontSize="large" />
              </AddColumnContainer>
            </Grid>
          </Grid>
          </>
        )}
        
        {tabValue === 'colours' && (
          <>
          <SubTitle>Colours</SubTitle>

          <Grid container direction="row" justify="center" spacing={2}>
            {colourColumns.map(col => (
              <Grid item>
                <PaperCard elevation={1}>
                  <Grid container direction="row" justify="space-between" alignItems="center">
                    <Grid item>
                      <TextFieldContainer>
                        <ColorPicker 
                          label={col.label}
                          defaultValue="#000"
                          value={config[col.key]}
                          TextFieldProps={{ value: config[col.key] }}
                          onChange={value => dispatcher.onChange(col.key, value)}
                        />
                      </TextFieldContainer>
                    </Grid>
  
                    <Grid item>
                      <IconHover display>
                        <SettingsBackupRestoreIcon onClick={() => dispatcher.onReset(col.key)} />
                      </IconHover>
                    </Grid>
                  </Grid>
                </PaperCard>
              </Grid>
            ))}
          </Grid>
          </>
        )}

        {tabValue === 'misc' && (
          <>
          <SubTitle>Miscellaneous</SubTitle>

          <Grid container direction="row" justify="center" spacing={2}>
            {miscColumns.map(col => (
              <Grid item>
                <PaperCard elevation={1}>
                  <Grid container direction="row" justify="space-between" alignItems="center" wrap="nowrap">
                    <Grid item style={{ flexGrow: '1', marginRight: '1rem' }}>
                      {col.options && col.options.length ? (
                        <FormControl fullWidth>
                          <InputLabel id={`${col.key}-select-label`}>{col.label}</InputLabel>
                          <Select
                            labelId={`${col.key}-select-label`}
                            id={`${col.key}-select`}
                            value={config[col.key]}
                            defaultValue={col.options[0].key}
                            onChange={e => dispatcher.onChange(col.key, e)}
                          >
                            {col.options.map(option => <MenuItem value={option.key}>{option.label}</MenuItem>)}
                          </Select>
                        </FormControl>
                      ) : (
                        <TextFieldContainer>
                          <TextField 
                            label={col.label}
                            value={config[col.key]}
                            type={col.type || 'text'}
                            onChange={e => dispatcher.onChange(col.key, e)}
                          />
                        </TextFieldContainer>
                      )}
                    </Grid>
  
                    <Grid item>
                      <IconHover display>
                        <SettingsBackupRestoreIcon onClick={() => dispatcher.onReset(col.key)} />
                      </IconHover>
                    </Grid>
                  </Grid>
                </PaperCard>
              </Grid>
            ))}
          </Grid>
          </>
        )}

        {tabValue === 'carousel' && (
          <>
          <SubTitle>Carousel</SubTitle>

          <Grid container direction="column" spacing={2}>
            <Grid item>
              <Grid container direction="row" justify="center" spacing={2}>
                <Grid item>
                  <PaperCard elevation={1}>
                    <FormControl fullWidth>
                      <InputLabel id="position-select-label">Position</InputLabel>
                      <Select
                        labelId="position-select-label"
                        id="position-select"
                        value={config.carouselPosition}
                        defaultValue='none'
                        onChange={e => dispatcher.onChange('carouselPosition', e)}
                      >
                        <MenuItem value={'none'}>None</MenuItem>
                        <MenuItem value={'top'}>Top</MenuItem>
                        <MenuItem value={'bottom'}>Bottom</MenuItem>
                      </Select>
                    </FormControl>
                  </PaperCard>
                </Grid>

                <Grid item>
                  <PaperCard elevation={1}>
                    <TextField 
                      label="Interval (ms)"
                      type='number'
                      value={config.carouselInterval || 4000}
                      onChange={e => dispatcher.onChange('carouselInterval', e)}
                    />
                  </PaperCard>
                </Grid>
              </Grid>
            </Grid>

            <Grid item>
              <Grid container direction="row" justify="center" spacing={2}>
                {config.carouselImages.map((image, index) => (
                  <Grid item>
                    <PaperCard elevation={1}>
                      <Grid container spacing={1} direction="column" justify="" alignItems="stretch">
                        <Grid item>
                          <Grid container direction="row" justify="space-between">
                            <Grid item>
                              <IconHover display={index !== 0}>
                                <ArrowBackIcon onClick={() => dispatcher.onShiftArray('carouselImages', index, -1)} />
                              </IconHover>
                            </Grid>
                            <Grid item>
                              <IconHover display>
                                <DeleteIcon onClick={() => dispatcher.onRemoveArrayObject('carouselImages', index)} />
                              </IconHover>
                            </Grid>
                            <Grid item>
                              <IconHover display={index !== config.carouselImages.length - 1}>
                                <ArrowForwardIcon onClick={() => dispatcher.onShiftArray('carouselImages', index, 1)} />
                              </IconHover>
                            </Grid>
                          </Grid>
                        </Grid>

                        <Grid item>
                          <TextField 
                            label="Image Src"
                            value={image.src}
                            onChange={e => dispatcher.onChangeArrayObject('carouselImages', e, index, 'src')}
                          />
                        </Grid>
                      </Grid>

                    </PaperCard>
                  </Grid>
                ))}

                <Grid item>
                  <AddCarouselImageContainer onClick={onAddCarouselImage}>
                    <AddCircleIcon fontSize="large" />
                  </AddCarouselImageContainer>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          </>
        )}

        {tabValue === 'weather' && (
          <>
          <SubTitle>Weather</SubTitle>

          <Grid container direction="row" justify="center" spacing={2}>
            {weatherColumns.map(col => (
              <Grid item>
                <PaperCard elevation={1}>
                  <Grid container direction="row" justify="space-between" alignItems="center" wrap="nowrap">
                    <Grid item style={{ flexGrow: '1', marginRight: '1rem' }}>
                      {col.options && col.options.length ? (
                        <FormControl fullWidth>
                          <InputLabel id={`${col.key}-select-label`}>{col.label}</InputLabel>
                          <Select
                            labelId={`${col.key}-select-label`}
                            id={`${col.key}-select`}
                            value={config[col.key]}
                            defaultValue={col.options[0].key}
                            onChange={e => dispatcher.onChange(col.key, e)}
                          >
                            {col.options.map(option => <MenuItem value={option.key}>{option.label}</MenuItem>)}
                          </Select>
                        </FormControl>
                      ) : (
                        <TextFieldContainer>
                          <TextField 
                            label={col.label}
                            value={config[col.key]}
                            type={col.type || 'text'}
                            onChange={e => dispatcher.onChange(col.key, e)}
                          />
                        </TextFieldContainer>
                      )}
                    </Grid>

                    <Grid item>
                      <IconHover display>
                        <SettingsBackupRestoreIcon 
                          onClick={() => dispatcher.onReset(col.key)} 
                        />
                      </IconHover>
                    </Grid>
                  </Grid>
                </PaperCard>
              </Grid>
            ))}
          </Grid>
          </>
        )}
      </PaperContainer>

      <CenterButtonContainer>
        <Button 
          color="primary"
          onClick={() => setShowPreview(x => !x)}
        >Toggle Preview</Button>
      </CenterButtonContainer>

      {showPreview && <RateBoard config={config} catalog={catalog} />}

      <CenterButtonContainer>
        <Button 
          variant="contained" 
          color="primary" 
          onClick={() => fileDownload(JSON.stringify(config), 'config.json')}
        >
          Download
        </Button>
      </CenterButtonContainer>
    </Container>
  )
}

export default Config