import { useState, useEffect } from 'react'
import {
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableFooter,
  Grid,
} from '@material-ui/core'
import Carousel from 'react-material-ui-carousel'
import styled from 'styled-components'

import WeatherWidget from './WeatherWidget'

const Container = styled.div`
  background-color: ${props => props.colour};
  min-height: 100vh;
  padding: 1rem;
`
const Image = styled.img`
  display: block;
  margin: auto;
  max-height: 10rem;
`
const CarouselImage = styled.img`
  display: block;
  margin: 0.5rem auto;
  height: 30rem;
`
const FlagImageCell = styled.img`
  box-shadow: 0px 0px 3px 1px #a2a2a2;
  width: ${props => {
    switch(props.size) {
      case 'small':
        return '2.5rem';
      case 'medium':
        return '5rem';
      case 'large':
        return '7.5rem';
      default:
        return '5rem';
    }
  }};
`
const FontCell = styled.div`
  font-size: ${props => {
    switch(props.size) {
      case 'small':
        return 'large';
      case 'medium':
        return 'x-large';
      case 'large':
        return 'xx-large';
      default:
        return 'x-large';
    }
  }};
`
const PaperContainer = styled(Paper)`
  overflow: auto;
  height: 100%;

  th {
    font-weight: bold;
  }
`
const ColouredTableCell = styled(TableCell)`
  color: ${props => props.fontColour} !important;
  background-color: ${props => props.colour};
  border-bottom-color: ${props => props.borderColour} !important;
`

const cellDisplay = (column, row) => {
  switch(column.key) {
    case 'flag':
      return <FlagImageCell
        size={column.size}
        src={row[column.key]}
        alt={row[column.key]}
      />
    default:
      return <FontCell size={column.size}>{row[column.key]}</FontCell>
  }
}

const ImageCarousel = ({ items, interval }) => {
  return (
    <Carousel
      stopAutoPlayOnHover={false}
      swipe={false}
      indicators={false}
      navButtonsAlwaysInvisible={true}
      interval={Number(interval) * 1000}
    >
      {items.map((item, i) => <CarouselImage key={i} src={item.src} />)}
    </Carousel>
  )
}

const RateBoard = ({ config, catalog, removeToken }) => {
  const [allRates, setAllRates] = useState([])

  useEffect(() => {
    if (config.isAllRates) {
      setAllRates(catalog.rates || [])
    } else {
      setAllRates((catalog.rates || []).filter(rate => rate.isFlagged))
    }
  }, [catalog.rates?.length])

  useEffect(() => {
    setTimeout(() => {
      const rates = [...allRates]
      if (rates.length > 0) {
        const firstRate = rates.shift();
        setAllRates([...rates, firstRate])
      }
    }, Number(config.rowsInterval) * 1000)
  }, [allRates, config.rowsInterval])

  const rates = allRates.slice(0, Number(config.maxRows));

  const tableHead = config.tableColumns || [];

  const timestamp = <FontCell>{catalog.timestamp}</FontCell> || '';

  return (
    <Container colour={config.backgroundColour}>
      <Image src={config.logo} alt="logo" />

      {config.carouselPosition === 'top' && (
        <ImageCarousel items={config.carouselImages} interval={Number(config.carouselInterval)} />
      )}

      <Grid container spacing={2}>
        {config.weatherPosition === 'left' && (
          <Grid item xs={2}>
            <PaperContainer style={{ 
              backgroundColor: config.weatherBackgroundColour, 
              color: config.weatherFontColour
            }}>
              <WeatherWidget 
                apiKey={config.weatherApiKey} 
                units={config.weatherUnits} 
                interval={config.weatherInterval}
              />
            </PaperContainer>
          </Grid>
        )}

        <Grid item xs={config.weatherPosition === 'none' ? 12 : 10}>
          <PaperContainer elevation={3}>
            <TableContainer>
              <Table>
                <TableHead>
                  {config.datePosition && config.datePosition.includes('top') && (
                    <TableRow>
                      <ColouredTableCell 
                        align={config.datePosition.split('-')[1]}
                        colSpan={tableHead.length}
                        colour={config.headerColour}
                        borderColour={config.dividerColour}
                        fontColour={'rgba(0, 0, 0, 0.54)'}
                      >
                        {timestamp}
                      </ColouredTableCell>
                    </TableRow>
                  )}
                  <TableRow onClick={removeToken}>
                    {tableHead.map((col, i) => (
                      <ColouredTableCell 
                        key={col.key} 
                        align={col.align} 
                        colour={config.headerColour}
                        borderColour={config.dividerColour}
                        fontColour={config.fontColour}
                        style={{ width: col.width }}
                      >
                        <FontCell>{col.title}</FontCell>
                      </ColouredTableCell>
                    ))}
                  </TableRow>
                </TableHead>

                <TableBody>
                  {rates.map((row, i) => (
                    <TableRow key={row.iso}>
                      {tableHead.map(col => (
                        <ColouredTableCell 
                          key={`${row.iso}-${col.key}`}
                          align={col.align}
                          colour={!!(i%2) ? config.oddColour : config.evenColour}
                          borderColour={config.dividerColour}
                          fontColour={config.fontColour}
                        >
                          {cellDisplay(col, row)}
                        </ColouredTableCell>
                      ))}
                    </TableRow>
                  ))}
                </TableBody>

                {config.datePosition && config.datePosition.includes('bottom') && (
                  <TableFooter>
                    <TableRow>
                      <ColouredTableCell 
                        align={config.datePosition.split('-')[1]}
                        colSpan={tableHead.length}
                        colour={config.footerColour}
                        borderColour={config.footerColour}
                        fontColour={config.font}
                      >
                        {timestamp}
                      </ColouredTableCell>
                    </TableRow>
                  </TableFooter>
                )}
              </Table>
            </TableContainer>
          </PaperContainer>
        </Grid>

        {config.weatherPosition === 'right' && (
          <Grid item xs={2}>
            <PaperContainer style={{ 
              backgroundColor: config.weatherBackgroundColour, 
              color: config.weatherFontColour
            }}>
              <WeatherWidget 
                apiKey={config.weatherApiKey} 
                units={config.weatherUnits} 
                interval={config.weatherInterval}
              />
            </PaperContainer>
          </Grid>
        )}
      </Grid>

      {config.carouselPosition === 'bottom' && (
        <ImageCarousel items={config.carouselImages} interval={config.carouselInterval} />
      )}
    </Container>
  );
}

export default RateBoard;
