import { useReducer } from 'react'

const changeArrayObject = (_items, { value, index, key }) => {
  const items = [..._items]
  items.splice(index, 1, {
    ...items[index],
    [key]: value
  })
  return items
}

const shiftArrayObject = (_items, { index, shift }) => {
  const items = [..._items]
  const item = items.splice(index, 1)
  items.splice(index + shift, 0, item[0])
  return items
}

const removeArrayObject = (_items, { index }) => {
  const items = [..._items]
  items.splice(index, 1)
  return items
}

const reducer = (_state, action) => {
  const state = JSON.parse(JSON.stringify(_state))

  const key = action.key;
  switch(action.type) {
    case 'setDefault':
      const defaultPayload = JSON.parse(JSON.stringify(action.payload))
      return { ...state, default: defaultPayload, current: defaultPayload }
    case 'onChange':
      state.current[key] = action.payload.value
      return state
    case 'onPushArray':
      state.current[key] = [ ...state.current[key], action.payload ]
      return state
    case 'onChangeArrayObject':
      state.current[key] = changeArrayObject(state.current[key], action.payload)
      return state
    case 'onShiftArray':
      state.current[key] = shiftArrayObject(state.current[key], action.payload)
      return state
    case 'onRemoveArrayObject':
      state.current[key] = removeArrayObject(state.current[key], action.payload)
      return state
    case 'onReset':
      state.current[key] = state.default[key]
      return state
    default:
      return state
  }
}

const useConfigReducer = () => {
  const initialState = {
    default: {},
    current: {},
  };
  const [state, dispatch] = useReducer(reducer, initialState);

  const dispatcher = {
    setDefault: (defaultConfig) => dispatch({
      type: 'setDefault', 
      payload: defaultConfig
    }),
    onChange: (key, event) => dispatch({
      key,
      type: 'onChange',
      payload: { value: (event.target ? event.target.value : event) }
    }),
    onPushArray: (key, payload) => dispatch({
      key, type: 'onPushArray', payload
    }),
    onChangeArrayObject: (key, event, index, objKey) => dispatch({
      key, 
      type: 'onChangeArrayObject',
      payload: { value: event.target.value, index, key: objKey }
    }),
    onShiftArray: (key, index, shift) => dispatch({
      key,
      type: 'onShiftArray',
      payload: { index, shift }
    }),
    onRemoveArrayObject: (key, index) => dispatch({
      key,
      type: 'onRemoveArrayObject',
      payload: { index }
    }),
    onReset: (key) => dispatch({
      key, type: 'onReset'
    })
  }

  return [state.current, dispatcher]
}

export default useConfigReducer