import {
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
} from '@material-ui/core'
import {
  ArrowBack as ArrowBackIcon,
  Delete as DeleteIcon,
  ArrowForward as ArrowForwardIcon,
} from '@material-ui/icons'

import { IconHover } from './styles'

const ColumnCard = ({ onColumnShift, onColumnRemove, onColumnChange, isFirst, isLast, column }) => {
  return (
    <Grid container spacing={1} direction="column" justify="" alignItems="stretch">
  
      <Grid item>
        <Grid container direction="row" justify="space-between">
          <Grid item>
            <IconHover display={!isFirst}>
              <ArrowBackIcon onClick={() => onColumnShift(-1)} />
            </IconHover>
          </Grid>
          <Grid item>
            <IconHover display>
              <DeleteIcon onClick={onColumnRemove} />
            </IconHover>
          </Grid>
          <Grid item>
            <IconHover display={!isLast}>
              <ArrowForwardIcon onClick={() => onColumnShift(1)} />
            </IconHover>
          </Grid>
        </Grid>
      </Grid>

      <Grid item>
        <FormControl fullWidth>
          <InputLabel id="field-select-label">Field</InputLabel>
          <Select
            labelId="field-select-label"
            id="field-select"
            value={column.key}
            onChange={e => onColumnChange(e, 'key')}
          >
            <MenuItem value={'flag'}>Flag</MenuItem>
            <MenuItem value={'iso'}>ISO</MenuItem>
            <MenuItem value={'name'}>Name</MenuItem>
            <MenuItem value={'country'}>Country</MenuItem>
            <MenuItem value={'weBuy'}>We Buy</MenuItem>
            <MenuItem value={'weSell'}>We Sell</MenuItem>
            <MenuItem value={'invBuy'}>Inv Buy</MenuItem>
            <MenuItem value={'invSell'}>Inv Sell</MenuItem>
          </Select>
        </FormControl>
      </Grid>

      <Grid item>
        <TextField 
          label="Field Name"
          value={column.title}
          onChange={e => onColumnChange(e, 'title')}
        />
      </Grid>

      <Grid item>
        <FormControl fullWidth>
          <InputLabel id="align-select-label">Align</InputLabel>
          <Select
            labelId="align-select-label"
            id="align-select"
            value={column.align || 'left'}
            onChange={e => onColumnChange(e, 'align')}
          >
            <MenuItem value={'center'}>Center</MenuItem>
            <MenuItem value={'left'}>Left</MenuItem>
            <MenuItem value={'right'}>Right</MenuItem>
          </Select>
        </FormControl>
      </Grid>

      <Grid item>
        <FormControl fullWidth>
          <InputLabel id="size-select-label">Size</InputLabel>
          <Select
            labelId="size-select-label"
            id="size-select"
            value={column.size || 'medium'}
            onChange={e => onColumnChange(e, 'size')}
          >
            <MenuItem value={'small'}>Small</MenuItem>
            <MenuItem value={'medium'}>Medium</MenuItem>
            <MenuItem value={'large'}>Large</MenuItem>
          </Select>
        </FormControl>
      </Grid>

      <Grid item>
        <TextField
          label="Width"
          value={column.width || ''}
          onChange={e => onColumnChange(e, 'width')}
        />
      </Grid>

    </Grid>
  )
}

export default ColumnCard