import { useEffect, useState } from 'react'
import axios from 'axios'
import styled from 'styled-components'
import {
  Grid
} from '@material-ui/core'

const Title = styled.h3`
  margin: 0;
`
const CurrentTemp = styled.h3`
  margin: 0;
`
const MinMaxTemp = styled.h5`
  margin: 0.5rem 0;
`

const baseUrl = 'https://api.openweathermap.org/data/2.5/weather'

const getWeather = (units, apiKey, setWeather) => {
  navigator.geolocation.getCurrentPosition(async position => {
    const lat = position.coords.latitude
    const lon = position.coords.longitude

    const res = await axios.get(`${baseUrl}?units=${units}&lat=${lat}&lon=${lon}&appid=${apiKey}`)
    let iconSrc = ''
    if (res.data.weather && res.data.weather[0]) {
      const icon = res.data.weather[0].icon
      iconSrc = `https://openweathermap.org/img/wn/${icon}@2x.png`
    }
    const { main, name } = res.data
    setWeather({
      iconSrc,
      name,
      temp: Math.round(main.temp),
      tempMin: Math.round(main.temp_min),
      tempMax: Math.round(main.temp_max)
    })
  })
}

const WeatherWidget = ({ apiKey, units, interval }) => {
  const [weather, setWeather] = useState({})

  const tempSuffix = {
    metric: '°C',
    imperial: '°F',
    standard: 'K'
  }[units]

  useEffect(() => {
    getWeather(units, apiKey, setWeather)
    const _interval = setInterval(() => {
      getWeather(units, apiKey, setWeather)
    }, (interval || 60 * 15) * 1000)
    
    return () => clearInterval(_interval)
  }, [apiKey, units, interval])

  return (
    <Grid container direction="column" justify="center" alignItems="center">
      {weather.iconSrc && (
        <Grid item>
          <img src={weather.iconSrc} alt="Weather Icon" />
        </Grid>
      )}

      <Grid item>
        <Title>{weather.name}</Title>
      </Grid>

      <Grid item>
        <CurrentTemp>{weather.temp}{tempSuffix}</CurrentTemp>
      </Grid>

      <Grid item>
        <MinMaxTemp>
          {weather.tempMin}{tempSuffix} - {weather.tempMax}{tempSuffix}
        </MinMaxTemp>
      </Grid>
    </Grid>
  )
}

export default WeatherWidget