import { useState, useEffect } from 'react';
import axios from 'axios';
import { parseStringPromise as parseXML } from 'xml2js';

const cleanCatalog = (catalog) => {
  const timestamp = catalog.TIMESTAMP[0];
  const rates = catalog.RATE.map(rate => ({
    isFlagged: rate.ISFLAGGED[0] === '**',
    flagURL: rate.FLAGURL[0],
    flag: rate.FLAGURL[0],
    iso: rate.ISO[0],
    name: rate.NAME[0],
    country: rate.COUNTRY[0],
    id: rate.ID ? rate.ID[0] : '',
    weBuy: rate.WEBUY[0],
    weSell: rate.WESELL[0],
    invBuy: rate.INVBUY[0],
    invSell: rate.INVSELL[0]
  }));

  return { timestamp, rates }
}

const useCatalog = (token) => {
  const [catalog, setCatalog] = useState({});

  const getCatalog = async () => {
    if (!token) return
    
    let ratesURL = `https://www.clearviewsys.com/rates/${token}/rateswithcss.xml`;
    if (process.env.NODE_ENV !== 'production') {
      ratesURL = 'rateswithcss.xml'
    }
    
    const res = await axios.get(ratesURL, {
      headers: { 'Cache-Control': 'no-cache' }
    });
    const xml = await parseXML(res.data);
    setCatalog(cleanCatalog(xml.CATALOG));
  };

  useEffect(() => {
    getCatalog()
    const interval = setInterval(() => getCatalog(), 5000)

    return () => clearInterval(interval)
  }, [token])

  return catalog
}

export default useCatalog