import { useState, useEffect } from 'react'

const useTokenStorage = () => {
  const [state, setState] = useState(null)

  useEffect(() => {
    setState(localStorage.getItem('token'))
  }, [])

  const setToken = (token) => {
    const confirmMessage = 'Are you sure you want to remove your token?'
    if (token === '' && !window.confirm(confirmMessage)) {
      return null;
    }
    localStorage.setItem('token', token)
    setState(token)
  }

  return [state, setToken]
}

export default useTokenStorage