import { useState } from 'react'
import {
  Paper,
  Grid,
  Modal as BaseModal,
  Fade as BaseFade,
  TextField,
  Button,
  CircularProgress
} from '@material-ui/core'
import styled from 'styled-components'
import axios from 'axios'

const Modal = styled(BaseModal)`
  display: flex;
  align-items: center;
  justify-content: center;
`
const Fade = styled(BaseFade)`
  :focus-visible {
    outline: none;
  }
`
const ModalPaper = styled(Paper)`
  padding: 1rem;
`

const TokenModal = ({ open, onSuccess }) => {
  const [token, setToken] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  const isValidToken = async () => {
    let configURL = `https://www.clearviewsys.com/rates/${token}/config.json`;
    if (process.env.NODE_ENV !== 'production') {
      configURL = 'config.jso'
    }
  
    try {
      await axios.get(configURL, {
        headers: { 'Cache-Control': 'no-cache' }
      });
      return true
    } catch {
      return false
    }
  }

  const onFormSubmit = async (event) => {
    event.preventDefault()
    setLoading(true)

    const isValid = await isValidToken()
    setLoading(false)
    if (isValid) {
      onSuccess(token)
    } else {
      setError('Invalid Token')
    }
  }

  return (
    <Modal
      open={open}
      closeAfterTransition
    >
      <Fade in={open}>
        <ModalPaper elevation={3}>
          <form onSubmit={onFormSubmit}>
            <Grid
              container
              direction="column"
              justify="center"
              alignItems="center"
              spacing={2}
            >
              <Grid item>
                <TextField 
                  variant="outlined" 
                  label="Token"
                  value={token}
                  onChange={event => setToken(event.target.value)}
                  error={!!error}
                  helperText={error}
                />
              </Grid>
              <Grid item>
                {loading ? (
                  <CircularProgress />
                ) : (
                  <Button type="submit">Submit</Button>
                )}
              </Grid>
            </Grid>
          </form>
        </ModalPaper>
      </Fade>
    </Modal>
  )
}

export default TokenModal