import useLocationHash from './hooks/useLocationHash'
import useTokenStorage from './hooks/useTokenStorage'
import useConfig from './hooks/useConfig'
import useCatalog from './hooks/useCatalog'

import TokenModal from './components/TokenModal'

import RateBoard from './pages/RateBoard/RateBoard'
import Config from './pages/Config/Config'

function App() {
  const hash = useLocationHash();
  const [token, setToken] = useTokenStorage();
  const config = useConfig(token);
  const catalog = useCatalog(token);
  
  if (!token) {
    return <TokenModal open={!token} onSuccess={token => setToken(token)} />
  }

  switch (hash) {
    case '#config':
      return <Config defaultConfig={config} catalog={catalog} />
    default: 
      return <RateBoard
        config={config}
        catalog={catalog}
        removeToken={() => setToken('')}
      />
  }
}

export default App;
